import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import CommonTable from "../../pages/table/CommonTable";
import CommonTableColumn from "../../pages/table/CommonTableColumn";
import CommonTableRow from "../../pages/table/CommonTableRow";
import axios from "axios";
import "./Post.css";

const PostList = (props) => {
  const [dataList, setDataList] = useState([]);

  const [showPopup, setShowPopup] = useState(false);

  const togglePopup = (event) => {
    setShowPopup("hi");
  };

  useEffect(() => {
    axios
      .get("http://localhost:8000/api/getInquerys")
      .then((response) => {
        setDataList(response.data.response);
      })
      .catch((Error) => {
        console.log(Error);
      });
  }, []);

  return (
    <>
      <div className="title">상담사 문의 목록</div>
      <CommonTable
        headersName={["글번호", "유저ID", "제목", "상담사ID", "등록일"]}
      >
        {dataList
          ? dataList.map((item, index) => {
              return (
                <CommonTableRow key={index}>
                  <CommonTableColumn>{item.id}</CommonTableColumn>
                  <CommonTableColumn>{item.userId}</CommonTableColumn>
                  <CommonTableColumn>
                    <Link
                      to={{
                        pathname: `/PostView`,
                        state: {
                          userId: item.userId,
                          title: item.title,
                          content: item.content,
                          counselorId: item.counselorId,
                          createdAt: item.createdAt,
                          updatedAt: item.updatedAt,
                        },
                      }}
                    >
                      {item.title}
                    </Link>
                  </CommonTableColumn>
                  <CommonTableColumn>
                    <button onClick={togglePopup}>
                      {item.counselorId == null ? "없음" : item.counselorId}
                    </button>
                  </CommonTableColumn>
                  <CommonTableColumn>{item.createdAt}</CommonTableColumn>

                  {showPopup ? (
                    <div className="popup">
                      <div className="popup_inner">
                        <h2>Success!</h2>
                        <button className="close" onClick={togglePopup}>
                          Close me
                        </button>
                      </div>
                    </div>
                  ) : null}
                </CommonTableRow>
              );
            })
          : ""}
      </CommonTable>
    </>
  );
};

export default PostList;
